/**
 * 100vhç”¨ã®CSSå¤‰æ•°
 */
// TODO: ä»¥ä¸‹ã®ã‚³ãƒ¼ãƒ‰ã‚’å¤–éƒ¨ã«ç§»è¡Œã—ãŸã„
updateViewport();

window.addEventListener('resize', updateViewport);

function updateViewport() {
  var vh = window.innerHeight / 100;
  var vw = window.innerWidth / 100;

  var root = document.documentElement;

  // å„ã‚«ã‚¹ã‚¿ãƒ ãƒ—ãƒ­ãƒ‘ãƒ†ã‚£ã«`window.innerHeight / 100`,`window.innerWidth / 100`ã®å€¤ã‚’ã‚»ãƒƒãƒˆ
  root.style.setProperty('--vh', vh + 'px');
  if (vh > vw) {
    root.style.setProperty('--vmax', vh + 'px');
    root.style.setProperty('--vmin', vw + 'px');
  } else {
    root.style.setProperty('--vmax', vw + 'px');
    root.style.setProperty('--vmin', vh + 'px');
  }
}
